.memory {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 300px;
  margin: auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.memory > .card {
  font-size: 2em;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
  -webkit-flex-basis: calc(100% / 6 - 0.4em);
          flex-basis: calc(100% / 6 - 0.4em);
  outline: 0.08em solid silver;
  margin: 0.2em;
  display: -webkit-flex;
  display: flex;
  cursor: default;
}

.memory > .card.hidden {
  background: silver;
}

.memory > .card.justMatched,
.memory > .card.justMismatched {
  outline: 0.1em solid green;
}
.memory > .card.justMismatched {
  outline-color: red;
}

.memory > .card.visible {
  cursor: not-allowed;
}

.memory > .card > .symbol {
  margin: auto;
}

.memory > .guesses {
  font-size: 1em;
  width: calc(100% - 0.4em);
  margin: 0.2em;
  text-align: right;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

.hallOfFame {
  width: 100%;
  border-collapse: collapse;
}

.hallOfFame .date,
.hallOfFame .guesses {
  width: 20%;
  text-align: center;
}

.hallOfFame tr:nth-child(even) {
  background: #eee;
}

.highScoreInput {
  font-size: 1em;
  width: calc(100% - 0.4em);
  margin: 0.2em;
}
.highScoreInput input {
  display: block;
  width: 100%;
  margin-bottom: 0.2em;
  font-family: Menlo, Monaco, Consolas, Inconsolata, 'Courier New', monospace;
}

